		previewImage(srcArray, index) {
			let imgArray = srcArray.map(e => e.url)
			uni.previewImage({
				current: index,
				urls: imgArray,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		}
